@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-v12-latin-300.woff2") format("woff2"), url("../fonts/inter-v12-latin-300.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-v12-latin-regular.woff2") format("woff2"), url("../fonts/inter-v12-latin-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-v12-latin-700.woff2") format("woff2"), url("../fonts/inter-v12-latin-700.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Pacifico";
  font-display: swap;
  src: url("../fonts/pacifico-v22-latin-regular.woff2") format("woff2"), url("../fonts/pacifico-v22-latin-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RubikOne";
  font-display: swap;
  src: url("../fonts/RubikOne-Regular.woff2") format("woff2"), url("../fonts/RubikOne-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #384a68;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #9ba4b3;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #384a68;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  z-index: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #fff5ff;
}
.wrapper::before {
  z-index: 0;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/background.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 35px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: relative;
  z-index: 2;
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 151px;
}

.header__logo {
  display: flex;
}

.header__logo-icon {
  margin-right: 33px;
}

.header__logo-text {
  margin-top: 7px;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  margin-left: 25px;
  margin-right: 25px;
}

.nav__link {
  display: block;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #9ba4b3;
}
@media (hover: hover) {
  .nav__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.nav__link.focus-visible:focus {
  opacity: 0.6;
}
.nav__link.focus-visible:active {
  color: #ff9ea8;
}

.nav__link--current {
  color: #ec75af;
}

.user-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main {
  position: relative;
  z-index: 2;
}

.slider {
  margin-bottom: 40px;
}

.slider__wrapper {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.slider__list {
  display: flex;
  margin: 0;
  padding: 30px 0;
  list-style: none;
}

.slider__item {
  width: 96px;
  height: 124px;
  padding: 30px 25px;
  margin-left: 10px;
  margin-right: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 16px rgba(77, 91, 163, 0.1);
  border-radius: 16px;
}

.slider__button {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  text-transform: uppercase;
  color: #cdd1d9;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.slider__button-text {
  font-size: 20px;
  line-height: 24px;
}

.slider__item--current {
  background-color: #ffffff;
}

.slider__item--current .slider__button {
  color: #2a9c9e;
}

.slider__toogle-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.slider__toogle {
  display: flex;
  align-items: center;
  padding: 10px 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  color: #384a68;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .slider__toogle:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.slider__toogle.focus-visible:focus {
  opacity: 0.6;
}
.slider__toogle.focus-visible:active {
  opacity: 0.3;
}

.slider__icon--back {
  margin-right: 10px;
  transform: rotate(180deg);
}

.slider__icon--forward {
  margin-left: 10px;
}

.content {
  margin-bottom: 40px;
}

.content__wrapper {
  display: flex;
  justify-content: space-between;
}

.content__item {
  position: relative;
  width: 32%;
  padding: 33px 60px 33px 41px;
  background-color: #ffffff;
  box-shadow: 0 5px 21px rgba(77, 91, 163, 0.1);
  border-radius: 21px;
}

.content__image {
  display: inline-flex;
  margin-right: 26px;
  margin-bottom: 22px;
  float: left;
}

.content__toogle {
  position: absolute;
  bottom: 23px;
  right: 27px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.footer {
  position: relative;
  z-index: 2;
  background-color: #ffffff;
  box-shadow: 0 -4px 16px rgba(77, 91, 163, 0.1);
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.social__item:not(:nth-of-type(3n)) {
  margin-right: 20px;
}

